a {
  border-bottom: solid 1px currentcolor;
  color: blue;
  text-decoration: none;

  &:hover {
    border-bottom: none;
  }

  &:visited {
    color: blue;
  }
}

blockquote {
  margin-bottom: 2rem;
  margin-left: 3rem;
  margin-top: 2rem;
}

cite {
  display: inline-block;
  margin-left: 3rem;
  margin-top: -1rem;
}

code {
  background-color: #ddd;
  border-radius: 0.25em;
  display: inline-block;
  font-size: 0.75em;
  font-family: menlo, monospace;
  padding: 0.25em;
  margin-bottom: 0.1em;
}

details {
  border: dotted 2px var(--black);
  padding: 0.5em;

  summary {
    cursor: pointer;

    & > * {
      display: inline;
    }
  }
}

figcaption {

  // font-sm
  font-size: 17px;
  letter-spacing: 0.015em;
  line-height: 1.3;
}

figure {
  margin: 2rem;
}

h1 {

  // font-lg
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 1.3;
}

h2 {

  // font-lg
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 1.3;
}

hr {
  border: none;
  border-bottom: solid 1px var(--black);
  margin-top: 2rem;
  margin-bottom: 2rem;
}

img {
  max-width: 100%;
}

li {
  max-width: 30em;
  margin-bottom: 0.5em;
}

p {
  margin-bottom: 1rem;
  margin-top: 1rem;
}

pre {
  code {
    display: block;
    padding: 1em;
  }
}

table {
  width: 100%;
}

td {
  width: 25%;
}

tr {
  border-top: solid 1px var(--black);
}

ul {
  padding-left: 1em;
}
